.header__title {
  background: linear-gradient(to right, #b40808 0%, #ff0000fb 54%, #fcb445f9 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-size: 500%;
  -webkit-background-clip: text;
  background-clip: text;
  animation: 3s ease-in-out infinite alternate filmoteka;
}

@keyframes filmSvg {
  0% {
    transform: rotateY(0);
  }

  100% {
    transform: rotateY(170deg);
  }
}

@keyframes filmoteka {
  0% {
    background-position: 0%;
  }

  100% {
    background-position: 100%;
  }
}

