    font-family: Alfa Slab One, cursive;
    font-size: 8vw;
    display: inline-block;
    position: relative;
  }

  .letter-space {
    visibility: hidden;
  }

  @keyframes waviy {
    0%, 40%, 100% {
      transform: translateY(0);
    }

    20% {
      transform: translateY(-20px);
    }
  }

  .swiper {
    width: 92%;
    margin: auto;
    display: flex;
    position: relative;
    overflow: hidden;
  }

  .swiper__wrapper {
    scrollbar-width: none;
    -ms-overflow-style: none;
    gap: 8px;
    display: flex;
  }

  .swiper__wrapper::-webkit-scrollbar {
    width: 0;
    height: 0;
  }

  .swiper__slide {
    display: flex;
  }

  .swiper__image {
    object-fit: contain;
    width: 250px;
  }

  .mobile-container {
    display: none;
  }
}

